.carrito-productos {
    .item-carrito {
        width: 100%;
        height: 80px;
        display: flex;
        margin-bottom: 10px;
        box-shadow: 0px 1px 2px #bbb;
        & > div {
            padding: 3px;
        }
        & div:nth-child(3),& div:nth-child(4){
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .producto-img {
            width: 20%;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        .producto-descripcion {
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            span {
                font-size: 0.85em;
            }
        }
        .boton-eliminar {
            width: 10%;
            color: #F53E3E;
        }
        .producto-precio {
            width: 20%;
        }
    }
    .item-total {
        display: flex;
        justify-content: center;
        align-items: center;
        & > div {
            text-align: center;
        }
    }
    
}