.contenedor-formulario {
/*     width: 40%; */
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    form {
        width: 350px;
        .form-group {
            display: flex;
            flex-direction: column;
            position: relative;
            margin: 20px 0px;
            font-family: 'poppins-medium';
            border: 1px solid #ddd;
            label {
                background-color: rgb(247, 243, 243);
                padding: 5px;
            }

            input {
                padding: 10px;
                border-top: 1px solid #ddd;
                outline: none;
            }
            .error {
                color: #d34f4f;
                font-size: 0.75em;
                position: absolute;
                top: 105%;
                left: 0;
            }
        }
        .submit-formulario {
            text-align: center;
            button {
                background-color: #5cc868;
                border-radius: 2px;
                color: #fff;
                font-size: 1em;
                padding: 5px;
                width: 200px;
                cursor: pointer;
                transition: all 100ms;
                &:hover {
                    background-color: #5ed46c; 
                }
            }
        }
    }
    
}

