.cabecera_mobile {
    background-color: #F53E3E;
    /* color: #fff; */
    font-size: 1.33em;
    position: relative;
    padding: 5px;
    .row-navbar {
        width: 100%;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 100;
        transition: left 300ms;
        .navbar {
            max-width: 400px;
            padding: 5px;
            width: 80%;
            background-color: #0E64B4;
            height: 100vh;
            h2 {
                margin-bottom: 10px;
            }
            ul {
                list-style: none;
                height: 300px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
            }
        }
    }
    .row-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .hamburger {
            width: 2em;
            height: 2em;
            img {
                width: 100%;
            }
        }
        .h1 {
            font-family: 'poppins-light';
            font-size: 1.5em;
            span {
                color: #0E64B4;
            }
        }
        
        .content_carrito_user {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .carrito {
                width: 1.2em;
                height: 1.2em;
                img {
                    width: 100%;
                }
            }
        }
        
    }
    .formulario {
        width: 90%;
        margin: 5px auto;
        height: 35px;
        display: flex;
        border-radius: 5px;
        /* overflow: hidden; */
        & > input,& > button {
            height: 100%;
            border: none;
            font-size: 0.8em;
        }
        input {
            width: 90%;
            outline: none;
            padding: 8px;
        }
        & > button {
            width: 10%;
            background-color: #3584FB;
            img {
                width: 50%;
                max-width: 21px;
            }
        }
    }
}


